<template>
	<div id="store-qualification">
		<public-header :title="store.title"></public-header>
		<div class="content">
			<div class="block">
				<div class="block-title">商家资质信息公示</div>
				<van-row gutter="10">
					<van-col span="12" v-if="store.qualification.business && store.qualification.business.thumb">
						<div class="thumb" @click="util.jsPreviewImage(store.qualification.business.thumb)" >
							<img :src="store.qualification.business.thumb" />
						</div>
					</van-col>
					<van-col span="12" v-if="store.qualification.service && store.qualification.service.thumb">
						<div class="thumb" @click="util.jsPreviewImage(store.qualification.service.thumb)" >
							<img :src="store.qualification.service.thumb" />
						</div>
					</van-col>
				</van-row>
			</div>
			<div class="block" v-if="(store.qualification.more1 && store.qualification.more1.thumb) || (store.qualification.more2 && store.qualification.more2.thumb)">
				<div class="block-title">更多资质信息公示</div>
				<van-row gutter="10">
					<van-col span="12" v-if="store.qualification.more1 && store.qualification.more1.thumb">
						<div class="thumb" @click="util.jsPreviewImage(store.qualification.more1.thumb)" >
							<img :src="store.qualification.more1.thumb" />
						</div>
					</van-col>
					<van-col span="12" v-if="store.qualification.more2 && store.qualification.more2.thumb">
						<div class="thumb" @click="util.jsPreviewImage(store.qualification.more2.thumb)" >
							<img :src="store.qualification.more2.thumb" />
						</div>
					</van-col>
				</van-row>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				sid: 0,
				store: {
					title: '食品安全档案',
					qualification: {}
				}
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'wmall/store/goods/qualification',
					data: {
						sid: that.sid
					},
					success: (res) => {
						that.store = that.util.extend(that.store, res.store);
					}
				})
			}
		},
		mounted() {
			var query = this.util.parseQuery(this.$route.query);
			this.sid = query.sid;
			this.onLoad();
		}

	}
</script>

<style >
	#store-qualification .block{
		margin: 10px;
		padding: 15px;
		background-color: #fff;
		border-radius: 5px;
	}
	#store-qualification .block-title{
		margin-bottom: 10px;
		font-size: 14px;
		font-weight: 500;
	}
	#store-qualification .block .thumb{
		position: relative;
		width: 100%;
		padding-top: 75%;
	}
	#store-qualification .block .thumb img{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 5px;
	}
</style>


